<template>
	<div class="container d-flex flex-column pb-0">
		<div class="content">
			<v-card tile flat class="pa-3">
				<v-row>
					<v-col sm="12" lg="8">
						<div class="d-flex flex-row">
							<div class="mr-3">
								<img src="/admin-static/logo.png" width="55px" />
							</div>
							<div class="d-flex flex-column justify-end">
								<div class="black--text subtitle-1 font-weight-medium">
									HỆ THỐNG QUẢN LÝ GIẢNG DẠY, ĐỒ ÁN VÀ DỊCH VỤ TRỰC TUYẾN
								</div>
								<div class="black--text subtitle-1">
									TRƯỜNG ĐẠI HỌC BÁCH KHOA HÀ NỘI - VIỆN CÔNG NGHỆ THÔNG TIN VÀ
									TRUYỀN THÔNG
								</div>
							</div>
						</div>
					</v-col>
					<v-col sm="12" lg="4">
						<div class="d-flex flex-column align-end">
							<div v-if="getUser && getUser.username">
								<v-icon class="mr-2"> mdi-face-profile</v-icon>
								{{ getUser.username }}
							</div>
							<div v-else>
								<v-icon class="mr-2"> mdi-face-profile</v-icon>
							</div>
							<BaseButton text="Đăng xuất" @click="logOut" />
						</div>
					</v-col>
				</v-row>
			</v-card>
			<v-tabs
				class="pa-3"
				background-color="#444"
				center-active
				dark
				hide-slider
        v-model="tab"
			>
				<v-tab @click="goToProjectList">
					<div class="action-hover ">
						Danh sách đồ án
					</div>
				</v-tab>
				<v-tab @click="goToProjectRegistration">
					<div class="action-hover ">
						Đăng ký nguyện vọng
					</div></v-tab
				>
				<v-tab @click="goToTopicList">
					<div class="action-hover ">
						Danh sách đề tài
					</div></v-tab
				>
				<v-tab @click="goToCompanyList">
					<div class="action-hover ">
						Danh sách doanh nghiệp
					</div></v-tab
				>
			</v-tabs>
			<v-main>
				<router-view></router-view>
			</v-main>
		</div>

		<div class="pa-3 pb-0">
			<BaseFooter />
		</div>
	</div>
</template>
<script>
import {mapActions, mapGetters} from 'vuex'
export default {
	data() {
		return {
			tab: null
		}
	},
	computed: {
		...mapGetters({
			getUser: 'auth/getUser'
		})
	},
	methods: {
		...mapActions({
			signOut: 'auth/signOut'
		}),
		goToProjectList() {
			if (this.$route.path !== '/projectList') this.$router.push('/projectList')
		},
		goToTopicList() {
			if (this.$route.path !== '/topicList') this.$router.push('/topicList')
		},
		goToProjectRegistration() {
			if (this.$route.path !== '/projectRegistration')
				this.$router.push('/projectRegistration')
		},
		goToCompanyList() {
			if (this.$route.path !== '/companyList') this.$router.push('/companyList')
		},
		async logOut() {
			this.$router.push('/login')
			await this.signOut()
		}
	}
}
</script>

<style lang="scss" scoped>
.content {
	min-height: calc(100vh - 70px);
}
.footer {
	height: 50px;
}
::v-deep .theme--dark.v-list-item--active:before,
.theme--light.v-list-item--active:hover:before,
.theme--light.v-list-item:focus:before {
	opacity: 0;
}
::v-deep .v-list-item--active.is-minimized {
	background-color: white;
	& .v-list-item__icon {
		color: #255cc2;
	}
}
::v-deep.v-toolbar--dense .v-toolbar__content,
.v-toolbar--dense .v-toolbar__extension {
	padding: 0 !important;
}
</style>
